import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {UserService} from "../../services/users";
import {Tools} from "../../services/tools";

/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  email: string;
  password: string;

  constructor(public navCtrl: NavController, public navParams: NavParams, public users: UserService, public tools: Tools) {

  }

  login() {


    let that = this;
    let promise  = new Promise((resolve, reject) => {

        that.users.ValidateEmail(that.email, that.password).then(response => {

          if(response == '200')
            resolve();
          if(response == '401')
            reject(response);

        }).catch(error => {

        });

    });
    promise.then(response => {

    }).catch(error => {
    this.tools.presentToast("bottom", "Sorry, we don't know that email");
      return false;
    });
  }



}



